import { Box } from '@mui/material';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import * as React from 'react';
import { TitleItem } from '../components/item/TitleItem';
import Footer from '../components/footer/Footer';

export const Privacy = () => {
  function Copyright() {
    return (
      <Typography variant="body2" color="text.secondary">
        {'Copyright © '}
        <Link color="inherit" href="https://blog.naver.com/free0210">
          gsp ict
        </Link>{' '}
        {new Date().getFullYear()}
        {'.'}
      </Typography>
    );
  }

  return (
    <>
      <Box
        component="div"
        sx={{
          mt: '5px',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <TitleItem>개인정보처리방침</TitleItem>
        <Typography variant="body" sx={{ mt: '5px', }}>
          <ul>
            <li>개인정보처리방침 : 개인정보를 취급하지 않습니다.</li>
            <li>개인정보 수집 : 개인정보를 수집하지 않습니다.</li>
            <li>개인정보 이용 : 개인정보를 이용하지 않습니다. </li>
          </ul>
        </Typography>
      </Box>
      <Footer />
    </>
  );
};

export default Privacy;
